
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import musicAny from '../../assets/mainBanner.png'
import musicAny2 from '../../assets/theme2.png'
import CardMusic from '../../components/CardMusic/index.jsx';


function ListOfSound() {
  return (
    <>
    <Container fluid>
<Row noGutters>
      <CardMusic
              id={1}
              titleCardMusic={"Le cours de la mthode"}
              imageCardMusic={musicAny}
              descriptionCardMusic={"Mthodologie pour l'exercice de la composition et de ltude critique de document"}
              />
        <CardMusic
        id={2}
        titleCardMusic={"La dimension politique de la guerre"}
        imageCardMusic={musicAny2}
        descriptionCardMusic={`Premier morceau consacr au programme de Terminale de HGGSP, "La dimension politique de la guerre" s'intresse  la faon avec laquelle la guerre a t conue en Occident et comment elle a volu depuis le XVIIIe sicle jusqu' nos jours. Le prochain morceau consacr au thme 2 du programme reprendra l'AXE 2 en s'intressant  "La construction de la paix".`}
        />
</Row>


    </Container>

    </>
  );
}

export default ListOfSound;